import Plotly from 'plotly.js/dist/plotly-basic.min.js'

export const AnalysisMixins = {
    data () {
        return {
            dataTablePageArray: [15, 25], // 全局的dataTable控制每页数量
            dataTableOptions: {
                stripe: true, // 是否为斑马纹
                loading: false, // 是否添加表格loading加载动画
                highlightCurrentRow: true, // 是否支持当前行高亮显示
                mutiSelect: false, // 是否支持列表项选中功能
                filter: false, // 是否支持数据过滤功能
                action: true, // 是否支持表格操作功能
                border: true // 是否支持外边框
            },
            pickerOptions: {
                disabledDate (time) {
                    return time.getTime() > Date.now()
                },
                shortcuts: [
                    // 日期选择器 选择时间
                    {
                        text: gettext('今天'),
                        onClick (picker) {
                            picker.$emit('pick', [new Date(), new Date()])
                        }
                    },
                    {
                        text: gettext('昨天'),
                        onClick (picker) {
                            const end = new Date()
                            const start = new Date()
                            start.setTime(start.getTime() - 3600 * 1000 * 24)
                            picker.$emit('pick', [start, end])
                        }
                    },
                    {
                        text: gettext('一周前'),
                        onClick (picker) {
                            const end = new Date()
                            const start = new Date()
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
                            picker.$emit('pick', [start, end])
                        }
                    },
                    {
                        text: gettext('一月前'),
                        onClick (picker) {
                            const end = new Date()
                            const start = new Date()
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
                            picker.$emit('pick', [start, end])
                        }
                    },
                    {
                        text: gettext('三月前'),
                        onClick (picker) {
                            const end = new Date()
                            const start = new Date()
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30 * 3)
                            picker.$emit('pick', [start, end])
                        }
                    }
                ]
            }
        }
    },
    methods: {
        getUTCTime (time) {
            time = time.slice() // 防止引用 重新创建
            time[0] = new Date(time[0]).setHours(0, 0, 0) // 将创建时间设置为0点0分0秒
            time[1] = new Date(time[1]).setHours(0, 0, 0) // 将创建时间设置为0点0分0秒
            return time
        },
        createPolt (id, total, tableData, title) {
            tableData.sort((val1, val2) => val1.value - val2.value)
            let groupData = []
            let idData = []
            let valueData = []
            let propData = []
            let widthList = []
            let size = null
            let length = tableData.length
            if (length === 1) {
                size = 0.1
            }
            else if (length < 3) {
                size = 0.2
            }
            else if (length < 4) {
                size = 0.3
            }
            else if (length < 5) {
                size = 0.5
            }
            else if (length < 10) {
                size = 0.7
            }
            else if (length < 15) {
                size = 0.9
            }
            else {
                size = 0.7
            }
            for (let i = 0; i < tableData.length; i++) {
                idData.push(tableData[i].code)
                groupData.push(tableData[i].name)
                let value = tableData[i].value
                valueData.push(value)
                let fixedVal = (100 * parseFloat(value) / parseFloat(total)).toFixed(2)
                if (isNaN(fixedVal)) {
                    fixedVal = 0
                }
                propData.push(gettext('数量:') + value + gettext(' 占比:') + fixedVal + '%')
                widthList.push(size)
            }
            const colors = ['#62d1de', '#54d6b6', '#a6db69', '#ffd454', '#ffa361', '#d1d1d1']
            const chartsData = [{
                x: valueData,
                y: groupData,
                type: 'bar',
                orientation: 'h',
                marker: colors,
                hoverinfo: 'text',
                hovertext: propData,
                opacity: 0.6,
                width: widthList,
                id: idData
            }]
            const layout = {
                width: 1000,
                height: 600,
                title: title,
                xaxis: {
                    tickvals: valueData,
                    automargin: true
                },
                yaxis: {
                    automargin: true,
                    ticklen: 12
                }
            }
            Plotly.newPlot(id, chartsData, layout, {displayModeBar: false})
            var myPlot = document.getElementById(id)
            var dragLayer = document.getElementsByClassName('nsewdrag')[0]
            myPlot.on('plotly_hover', function (data) {
                dragLayer.style.cursor = 'pointer'
            })
            myPlot.on('plotly_unhover', function (data) {
                dragLayer.style.cursor = ''
            })
        },
        clearPlot (id) {
            Plotly.purge(id)
        }
    }
}
