<template>
    <div class="content-box">
        <div class="content-wrap">
            <div class="content-dimesion" v-bkloading="{isLoading: isCateLoading, opacity: 1}">
                <div class="clearfix">
                    <div class="content-title">{{i18n.flowCategory}}</div>
                    <div class="content-date">
                        <div class="content-date-business">
                            <bk-dropdown-menu ref="dropdown" trigger="click">
                                <span slot="dropdown-trigger">
                                    <span class="dropdown-cursor">{{choiceBusinessName? choiceBusinessName : i18n.chooseBusiness}}</span>
                                    <i :class="['bk-icon icon-angle-down', {'icon-flip': isDropdownShow}]"></i>
                                </span>
                                <ul class="bk-dropdown-list" slot="dropdown-content">
                                <li v-for="item in businessList" :key="item.cc_id">
                                    <a href="javascript:void(0);" @click="onTemplateCategory(item.cc_id, item.cc_name)">{{item.cc_name}}</a>
                                </li>
                             </ul>
                            </bk-dropdown-menu>
                        </div>
                        <div class="content-date-picker" @click="onDatePickerClick">
                            <el-date-picker
                                ref="datePickerRef"
                                clear-icon="bk-icon icon-angle-down"
                                v-model="time"
                                align="center"
                                type="daterange"
                                format="yyyy-MM-dd"
                                value-format="timestamp"
                                :picker-options="pickerOptions"
                                :clearable="false"
                                :editable="false"
                                @change="onTemplateCategory()">
                            </el-date-picker>
                            <i :class="['bk-icon icon-angle-down', {'icon-flip': isDropdownShow}]"></i>
                        </div>
                    </div>
                </div>
                <data-statistics :dimensionList="classiFicationArray" :totalValue="ficationTotal"></data-statistics>
            </div>
            <div class="content-wrap-right" v-bkloading="{isLoading: isBussLoading, opacity: 1}">
                <div class="clearfix">
                    <div class="content-title">{{i18n.flowBusiness}}</div>
                    <div class="content-statistics">
                        <div class="content-business">
                            <bk-dropdown-menu ref="isDropDropdown" trigger="click">
                                <span slot="dropdown-trigger">
                                    <span class="dropdown-cursor">{{chooseCategoryName? chooseCategoryName : i18n.chooseCategory}}</span>
                                    <i :class="['bk-icon icon-angle-down', {'icon-flip': isDropdownShow}]"></i>
                                </span>
                                <ul class="bk-dropdown-list" slot="dropdown-content">
                                    <li v-for="item in categoryList" :key="item.value">
                                        <a href="javascript:void(0);" @click="onTemplateBizzCcId(item.value, item.name)">{{item.name}}</a>
                                    </li>
                                </ul>
                            </bk-dropdown-menu>
                        </div>
                        <div class="content-business-picker" @click="onTemplatePickerClick">
                            <el-date-picker
                                ref="businessPickerRef"
                                v-model="businessTime"
                                align="center"
                                type="daterange"
                                format="yyyy-MM-dd"
                                value-format="timestamp"
                                :picker-options="pickerOptions"
                                :clearable="false"
                                :editable="false"
                                @change="onTemplateBizzCcId()">
                            </el-date-picker>
                            <i :class="['bk-icon icon-angle-down', {'icon-flip': chooseDownShow}]"></i>
                        </div>
                    </div>
                </div>
                <data-statistics :dimensionList="taskStatistArray" :totalValue="total"></data-statistics>
            </div>
        </div>
        <div class="content-process-detail">
            <bk-tab :type="'fill'" :active-name="tabName" @tab-changed="onChangeTabPanel">
                <bk-tabpanel name="processDetails" :title="i18n.node">
                    <div class="content-wrap-detail">
                        <div class="content-wrap-from">
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.timeLimit}}</label>
                                <el-date-picker
                                    v-model="time"
                                    align="center"
                                    type="daterange"
                                    format="yyyy-MM-dd"
                                    value-format="timestamp"
                                    :picker-options="pickerOptions"
                                    :clearable="false"
                                    :editable="false"
                                    @change="onTemplateNode">
                                </el-date-picker>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseBusiness}}</label>
                                <el-select
                                    v-model="bizCcId"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onTemplateNode">
                                    <el-option
                                        v-for="item in bizList"
                                        :key="item.cc_id"
                                        :label="item.cc_name"
                                        :value="item.cc_id">
                                    </el-option>
                                </el-select>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseCategory}}</label>
                                <el-select
                                    v-model="category"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onTemplateNode">
                                    <el-option
                                        v-for="item in categorys"
                                        :key="item.value"
                                        :label="item.name"
                                        :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                        <data-table-pagination
                            v-bkloading="{isLoading: isDetailLoading, opacity: 1}"
                            :data="nodeData"
                            :total="nodeTotal"
                            :options="dataTableOptions"
                            :pagination="nodePagination"
                            :columns="nodeColumns"
                            :operates="nodeOperates"
                            @handleSortChange="onNodeSortChange"
                            @handleSizeChange="onNodeHandleSizeChange"
                            @handleIndexChange="onNodeHandleIndexChange">
                        </data-table-pagination>
                    </div>
                </bk-tabpanel>
                <bk-tabpanel name="processReference" :title="i18n.cite">
                    <div class="content-wrap-detail">
                        <div class="content-wrap-from">
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.timeLimit}}</label>
                                <el-date-picker
                                    v-model="time"
                                    align="center"
                                    type="daterange"
                                    format="yyyy-MM-dd"
                                    value-format="timestamp"
                                    :picker-options="pickerOptions"
                                    :clearable="false"
                                    :editable="false"
                                    @change="onTemplateByCiteData">
                                </el-date-picker>
                            </div>
                            <div class="content-wrap-select">
                                <label class="content-detail-label">{{i18n.chooseBusiness}}</label>
                                <el-select
                                    v-model="bizCcId"
                                    filterable
                                    :placeholder="i18n.choice"
                                    clearable
                                    @change="onTemplateByCiteData">
                                    <el-option
                                        v-for="item in bizList"
                                        :key="item.cc_id"
                                        :label="item.cc_name"
                                        :value="item.cc_id">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                        <data-table-pagination
                            v-bkloading="{isLoading: isReferLoading, opacity: 1}"
                            :data="citeData"
                            :total="citeTotal"
                            :pagination="citePagination"
                            :columns="citeColumns"
                            @handleSortChange="onCiteSortChange"
                            @handleSizeChange="onCiteHanldSizeChange"
                            @handleIndexChange="onCiteHandleIndexChange">
                        </data-table-pagination>
                    </div>
                </bk-tabpanel>
            </bk-tab>
        </div>
    </div>
</template>

<script>
import '@/utils/i18n.js'
import tools from '@/utils/tools.js'
import DataStatistics from '../dataStatistics/index.vue'
import {mapActions, mapState} from 'vuex'
import {AnalysisMixins} from '@/mixins/js/analysisMixins.js'
import DataTablePagination from '@/components/common/dataTable/DataTablePagination.vue'
import { errorHandler } from '@/utils/errorHandler.js'

export default {
    name: 'StatisticsTemplate',
    components: {
        DataStatistics,
        DataTablePagination
    },
    mixins: [AnalysisMixins],
    data () {
        return {
            i18n: {
                flowCategory: gettext('流程分类'),
                flowBusiness: gettext('所属业务'),
                chooseCategory: gettext('选择分类'),
                chooseBusiness: gettext('选择业务'),
                timeLimit: gettext('时间范围'),
                node: gettext('流程详情'),
                prop: gettext('所属业务'),
                cite: gettext('流程引用'),
                choice: gettext('请选择')
            },
            business: '',
            businessId: '',
            bizCcId: '',
            isDropdownShow: false,
            isTempalteShow: false,
            isCateLoading: true,
            isBussLoading: true,
            isReferLoading: true,
            isDetailLoading: true,
            chooseDownShow: false,
            datePickerRefShow: false,
            businessPickerRefShow: false,
            defaultDate: '',
            startDate: '',
            endDate: '',
            choiceBusinessName: '',
            listLoading: false,
            category: '',
            classiFicationArray: [],
            taskStatistArray: [],
            defaultDemo: {
                selected: 0
            },
            citeData: [],
            nodeData: [],
            time: [0, 0],
            businessTime: [0, 0],
            nodeTotal: 0,
            nodePageIndex: 1,
            nodeLimit: 15, // 每页数量
            nodeOrderBy: '-templateId',
            citeOrderBy: '-templateId',
            tabName: 'processDetails',
            nodePagination: {
                limit: this.nodeLimit,
                pageIndex: this.nodePageIndex,
                pageArray: this.dataTablePageArray
            },
            citeTotal: 0,
            citePageIndex: 1,
            citeLimit: 15,
            citePagination: {
                limit: this.citeLimit,
                pageIndex: this.citePageIndex,
                pageArray: this.dataTablePageArray // 公共js文件获取
            },
            nodeColumns: [
                {
                    prop: 'templateName',
                    label: gettext('流程名称'),
                    width: 285,
                    title: 'templateName',
                    router: (row, column, cellValue, index) => {
                        return `/template/edit/${row.businessId}/?template_id=${row.templateId}`
                    },
                    formatter: (row, column, cellValue, index) => {
                        return `<a class="template-router" href="/template/edit/${row.businessId}/?template_id=${row.templateId}">${row.templateName}</a>`
                    }
                },
                {
                    prop: 'businessName', // 识别id
                    label: gettext('业务'), // 表头显示名称
                    align: 'center' // 对其格式，可选（right，left，center）
                },
                {
                    prop: 'category',
                    label: gettext('分类'),
                    align: 'center'
                },
                {
                    prop: 'atomTotal',
                    label: gettext('原子数'),
                    sortable: 'custom',
                    align: 'center'
                },
                {
                    prop: 'subprocessTotal',
                    label: gettext('子流程数'),
                    sortable: 'custom',
                    align: 'center'
                },
                {
                    prop: 'gatewaysTotal',
                    label: gettext('网关数'),
                    sortable: 'custom',
                    align: 'center'
                },
                {
                    prop: 'creator',
                    label: gettext('创建人'),
                    align: 'center'
                },
                {
                    prop: 'createTime',
                    label: gettext('创建时间'),
                    align: 'center',
                    width: 220
                }
            ],
            nodeOperates: {
                width: 160,
                isShow: true,
                data: [
                    {
                        label: gettext('执行历史'),
                        show: true,
                        cls: 'bk-button btn-size-mini ',
                        method: (index, row) => {
                            this.onInstanceHandleView(index, row)
                        }
                    }
                ]
            },
            total: 0,
            ficationTotal: 0,
            citeColumns: [
                {
                    prop: 'templateName',
                    label: gettext('流程名称'),
                    width: 285,
                    router: (row, column, cellValue, index) => {
                        return `/template/edit/${row.businessId}/?template_id=${row.templateId}`
                    },
                    formatter: (row, column, cellValue, index) => {
                        return `<a class="template-router" href="/template/edit/${row.businessId}/?template_id=${row.templateId}">${row.templateName}</a>`
                    }
                },
                {
                    prop: 'templateId',
                    label: 'ID',
                    align: 'center'
                },
                {
                    prop: 'appmakerTotal',
                    label: gettext('创建轻应用个数'),
                    align: 'center',
                    sortable: 'custom'
                },
                {
                    prop: 'relationshipTotal',
                    label: gettext('被引用为子流程个数'),
                    align: 'center',
                    sortable: 'custom'
                },
                {
                    prop: 'instanceTotal',
                    label: gettext('创建任务实例个数'),
                    align: 'center',
                    sortable: 'custom'
                }
            ],
            chooseCategoryName: ''
        }
    },
    computed: {
        ...mapState({
            bizList: state => state.bizList,
            categorys: state => state.categorys,
            site_url: state => state.site_url
        }),
        businessList () {
            if (this.bizList.length === 0) {
                this.getBizList()
            }
            const list = tools.deepClone(this.bizList)
            list.unshift({cc_id: undefined, cc_name: gettext('全部')})
            return list
        },
        categoryList () {
            if (this.categorys.length === 0) {
                this.getCategorys()
            }
            const list = tools.deepClone(this.categorys)
            list.unshift({value: undefined, name: gettext('全部')})
            return list
        }
    },
    mounted () {
        this.onTemplateCategory()
        this.onTemplateNode()
        this.onTemplateBizzCcId()
    },
    created () {
        this.getDateTime()
    },
    methods: {
        ...mapActions([
            'getBizList',
            'getCategorys'
        ]),
        ...mapActions('template/', [
            'queryTemplateData'
        ]),
        onTemplateCategory (business, name) {
            if (!Array.isArray(name)) {
                this.choiceBusinessName = name
            }
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'category',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    biz_cc_id: business
                })
            }
            this.templateData(data)
        },
        onTemplateNode () {
            this.isDetailLoading = true
            try {
                let time = this.getUTCTime(this.time)
                const data = {
                    group_by: 'template_node',
                    conditions: JSON.stringify({
                        create_time: time[0],
                        finish_time: time[1],
                        biz_cc_id: this.bizCcId,
                        category: this.category,
                        order_by: this.nodeOrderBy
                    }),
                    pageIndex: this.nodePageIndex,
                    limit: this.nodeLimit
                }
                this.templateTableData(data)
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isDetailLoading = false
            }
        },
        onTemplateBizzCcId (category, name) {
            if (!Array.isArray(name)) {
                this.chooseCategoryName = name
            }
            let time = this.getUTCTime(this.businessTime)
            const data = {
                group_by: 'biz_cc_id',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    category: category
                })
            }
            this.templateBizIdData(data)
        },
        async templateBizIdData (data) {
            this.isBussLoading = true
            try {
                const templateData = await this.queryTemplateData(data)
                this.taskStatistArray = templateData.data.groups
                this.total = templateData.data.total
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isBussLoading = false
            }
        },
        async templateData (data) {
            this.isCateLoading = true
            try {
                const templateData = await this.queryTemplateData(data)
                this.classiFicationArray = templateData.data.groups
                this.ficationTotal = templateData.data.total
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isCateLoading = false
            }
        },
        async templateTableData (data) {
            const templateData = await this.queryTemplateData(data)
            switch (data.group_by) {
                case 'template_node':
                    this.nodeData = templateData.data.groups
                    this.nodeTotal = templateData.data.total
                    break
                case 'template_cite':
                    this.citeData = templateData.data.groups
                    this.citeTotal = templateData.data.total
                    break
            }
        },
        onTemplateByCiteData () {
            this.isReferLoading = true
            try {
                let time = this.getUTCTime(this.time)
                const data = {
                    group_by: 'template_cite',
                    conditions: JSON.stringify({
                        create_time: time[0],
                        finish_time: time[1],
                        biz_cc_id: this.bizCcId,
                        category: this.category,
                        order_by: this.citeOrderBy
                    }),
                    pageIndex: this.citePageIndex,
                    limit: this.citeLimit
                }
                this.templateTableData(data)
            } catch (e) {
                errorHandler(e, this)
            } finally {
                this.isReferLoading = false
            }
        },
        // 流程引用的排序
        onCiteSortChange (column, prop, order) {
            order = column[0].order === 'ascending' ? '' : '-'
            this.citeOrderBy = column[0].prop ? order + column[0].prop : '-templateId'
            this.onTemplateByCiteData()
        },
        onCiteSizeChange (limit) {
            this.citePageIndex = 1
            this.citeLimit = limit
            this.onTemplateByCiteData()
        },
        onCiteHandleIndexChange (pageIndex) {
            this.citePageIndex = pageIndex
            this.onTemplateByCiteData()
        },
        onNodeSortChange (column, prop, order) {
            order = column[0].order === 'ascending' ? '' : '-'
            this.nodeOrderBy = column[0].prop ?  order + column[0].prop : '-templateId'
            this.onTemplateNode()
        },
        onNodeHandleSizeChange (limit) {
            this.nodePageIndex = 1
            this.nodeLimit = limit
            this.onTemplateNode()
        },
        onNodeHandleIndexChange (pageIndex) {
            this.nodePageIndex = pageIndex
            this.onTemplateNode()
        },
        onChangeTabPanel (name) {
            this.tabName  = name
            if (name === 'processDetails') {
                this.onTemplateNode()
            } else {
                this.onTemplateByCiteData()
            }
        },
        getDateTime () {
            let date = new Date()
            date.setHours(0, 0, 0)
            this.time[1] = Date.parse(date)
            this.businessTime[1] = Date.parse(date)
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 30)
            this.time[0] = Date.parse(date)
            this.businessTime[0] = Date.parse(date)
        },
        onInstanceHandleView (index, row) {
            window.open(this.site_url + 'taskflow/home/' + row.businessId + '/?template_id=' + row.templateId)
        },
        onDatePickerClick () {
            this.datePickerRefShow = !this.datePickerRefShow
            this.$refs.datePickerRef.pickerVisible = this.datePickerRefShow
        },
        onTemplatePickerClick () {
            this.businessPickerRefShow = !this.businessPickerRefShow
            this.$refs.businessPickerRef.pickerVisible = this.businessPickerRefShow
        }
    }
}
</script>
